* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

// Colors
$primary-color: #0377bd;
$value-background: #d9d9d99a;
$white-color: #fff;
$success-color: #22c55e;
$danger-color: #ef4444;
body {
  background: $primary-color;
  overflow-x: hidden;
}

// Media
// Small phones
$screen-tn-max: 450px;
// Small tablets and large smartphones (landscape view)
$screen-sm-max: 596px;
// Small tablets (portrait view)
$screen-md-max: 768px;
// Tablets and small desktops
$screen-lg-max: 992px;

// Phone devices
@mixin tn {
  @media (max-width: #{$screen-tn-max}) {
    @content;
  }
}

// Small devices
@mixin sm {
  @media (max-width: #{$screen-sm-max}) {
    @content;
  }
}

// Medium devices
@mixin md {
  @media (max-width: #{$screen-md-max}) {
    @content;
  }
}

// Large devices
@mixin lg {
  @media (max-width: #{$screen-lg-max}) {
    @content;
  }
}

@mixin centerChilds {
  display: flex;
  justify-content: center;
  align-items: center;
}

// Scroll bar
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  transition: 0.5s;
  background: #888;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

:root {
  font-size: 18px;
  scroll-behavior: smooth;

  @include lg {
    font-size: 18px;
  }
  @include md {
    font-size: 16px;
  }
  @include sm {
    font-size: 14px;
  }
  // @include tn{
  //     font-size: 10px;
  // }
}
